<script>
    import { link } from "svelte-spa-router";
    let isMobile = window.innerWidth < 769;
    let isMenuOpen = false;
</script>

<header>
    <div class="container">
        <a href="/" use:link>
            {#if isMobile}<img src="./assets/Logo.svg" alt="Tulpo" />{:else}
                <img src="./assets/LogoMobile.svg" alt="Tulpo" />{/if}
        </a>
        <nav class:show={isMenuOpen}>
            {#if isMobile}
                <img
                    src="./assets/IconClose.svg"
                    alt="Close"
                    class="close"
                    on:click={() => (isMenuOpen = false)}
                />
            {/if}
            <ul>
                <li><a href="/" use:link>Wallet</a></li>
                <li><a href="/" use:link>Business Solutions</a></li>
                <li><a href="/" use:link>Become a Partner</a></li>
                <li><a href="/" use:link>Contact Us</a></li>
            </ul>
        </nav>
        {#if isMobile}
            <img
                src="./assets/IconBurger.svg"
                alt="Menu"
                on:click={() => (isMenuOpen = true)}
            />
        {/if}
    </div>
</header>

<style lang="scss">
    @import "./header.scss";
</style>
